import { Component, OnInit } from '@angular/core';
import { PersonserviceService } from './../personservice.service';

@Component({
  selector: 'app-pop',
  templateUrl: './pop.component.html',
  styleUrls: ['./pop.component.css']
})
export class PopComponent implements OnInit {
  persons=[];
  itemcounter : number;
  projecttext : string ="Angular Project";
  projects = [];

  constructor(private _personservice:PersonserviceService ) { }

  ngOnInit(): void {
    this.itemcounter = this.projects.length;
    this.persons =this._personservice.getdata();
  }

  addproj(){
    this.projects.push(this.projecttext); // add project list
    this.projecttext = ''; // empty arrey for next 
    this.itemcounter = this.projects.length; // add another count
  }


  removeproj(i)
  {
    this.projects.splice(i,1);
    this.itemcounter = this.projects.length;
  }
}
